import * as mapboxgl from 'mapbox-gl'
import source from './source'
import lnglat from './lnglat'
import './mapbox-gl.css'
import { LngLat, MapCoordinate } from './type'
// import mapboxLanguage from '@mapbox/mapbox-gl-language';
// @ts-ignore
mapboxgl.accessToken = source.token

interface IMapOpt extends mapboxgl.MapboxOptions {
  container: HTMLElement // 地图容器
}
const defaults = {
  center: [0, 0] as mapboxgl.LngLatLike,
}

class MapService {
  timer: number
  constructor(coordinate: MapCoordinate = 'WGS84') {
    // 设置地图坐标系
    this.coordinate = coordinate
    lnglat.coordinate = this.coordinate
    return this
  }

  private map: mapboxgl.Map
  private coordinate: MapCoordinate

  loadMap = (opt: IMapOpt) => {
    const _opt = { ...defaults, ...opt }
    const { center } = _opt

    const map = new mapboxgl.Map({
      ..._opt,
      center: lnglat.format(center) as mapboxgl.LngLatLike,
    })

    this.timer = window.setTimeout(() => {
      alert('地图加载较慢，请检查网络')
    }, 1000 * 30)

    // set language
    // const language = new mapboxLanguage();
    // map.addControl(language);

    // @ts-ignore
    // window.map = map;
    this.map = map

    // 全屏组件
    // map.addControl(new mapboxgl.FullscreenControl(), 'bottom-left');

    // Nav
    // const nav = new mapboxgl.NavigationControl();
    // map.addControl(nav, 'top-left');

    return new Promise(resolve => {
      map.on('load', () => {
        resolve()
        clearTimeout(this.timer)
      })
    })
  }

  getMap = () => this.map

  setCenterZoom = (center: LngLat, zoom: number) => {
    this.map.setZoom(zoom)
    this.map.panTo(lnglat.format(center) as mapboxgl.LngLatLike)
  }

  addMarker = (markers: mapboxgl.Marker[]) => {
    markers.forEach(marker => marker.addTo(this.map))
  }

  removeMarker = (markers: mapboxgl.Marker[]) => {
    markers.forEach(marker => marker.remove())
  }

  // 销毁map
  destroy = () => {
    clearTimeout(this.timer)

    delete this.map
  }
}

// @ts-ignore
// window.map = map;

export default MapService
