import * as React from 'react'
import MapService from '@map/index'
import * as styles from './style.module.scss'

export default function Map() {
  const wrpRef = React.useRef(null)

  React.useEffect(() => {
    new MapService().loadMap({
      container: wrpRef.current!,
      center: [120.172217, 30.245855],
      style: 'mapbox://styles/gaea1/cjq7ptw8ka4f12sqsgnwq06yo',
      zoom: 13,
      pitch: 50,
      maxZoom: 17,
      minZoom: 11,
    })
  }, [])

  return <div ref={wrpRef} className={styles.mapWrapper} />
}
