/**
 * 坐标系定义
 * 百度坐标系 (BD-09)  火星坐标系 (GCJ-02)  WGS84
 */

import * as mapboxgl from 'mapbox-gl'
import coordtransform from './coordtransform'
import { LngLat, MapCoordinate } from './type'

const lnglat = {
  coordinate: 'WGS84' as MapCoordinate,

  format: (ll: LngLat | LngLat[] | mapboxgl.LngLatLike) => {
    if (lnglat.coordinate === 'WGS84') {
      if (Array.isArray(ll[0])) {
        console.log()
        return (ll as LngLat[]).map(
          item => [+item[0], +item[1]] as mapboxgl.LngLatLike
        )
      }
      return [+ll[0], +ll[1]] as mapboxgl.LngLatLike
    }

    if (lnglat.coordinate === 'GCJ-02') {
      if (Array.isArray(ll[0])) {
        return (ll as LngLat[]).map(item =>
          coordtransform.gcj02towgs84(item[0], item[1])
        ) as mapboxgl.LngLatLike[]
      }

      return coordtransform.gcj02towgs84(ll[0], ll[1]) as mapboxgl.LngLatLike
    }
    return [0, 0] as mapboxgl.LngLatLike
  },
}

export default lnglat
